$main-color: #d14609; // The Main Color
$sec-color: #772A16;  // Secondary Color
$third-color: #E9DDCD; // Third Color
$g-color-2: #782805;  // Gradient Color 1
$g-color-1: #d24a00;  // Gradient Color 2
$bg-color-g1: #fd5e09; // Background Gradien Color 1
$bg-color-g2: #c13005; // Background Gradien Color 2
$nav-bg: #262626; // Navigation Bar Background Color
$bg-color: #1b1b1b; // Background Color
$sm-text-1: #898989; // First Small Text Color
$sm-text-2: #bdb8af; // Second Small Text Color
$input-bg: #383838; // Input Background Color

@mixin main-gradient($direction) {
    background-image: linear-gradient($direction,#fd5e09 0%,#c13005 100%);
}
@mixin bg-gradient($direction) {
    background-image: linear-gradient($direction,#d24a00 0%,#782805 100%);
}

// Add Roboto Fonts
@font-face {
    font-family: Roboto-Thin;
    src: url('../fonts/Roboto-Thin.ttf');
}
@font-face {
    font-family: Roboto-Regular;
    src: url('../fonts/Roboto-Regular.ttf');
}
@font-face {
    font-family: Roboto-Medium;
    src: url('../fonts/Roboto-Medium.ttf');
}
@font-face {
    font-family: Roboto-Light;
    src: url('../fonts/Roboto-Light.ttf');
}
@font-face {
    font-family: Roboto-Bold;
    src: url('../fonts/Roboto-Bold.ttf');
}

body {
    font-family: Roboto-Regular;
    font-size: 0.9rem;
    background-color: $bg-color;
}
img[alt="Free Web Hosting"] {
    display: none;
}
// Scroll Bar
::-webkit-scrollbar {
    width: 5px;
}
::-webkit-scrollbar-track {
    background-color: transparent;
}
::-webkit-scrollbar-thumb {
    background-color: $main-color;
    border-radius: 5px;
}

// Hr
.hr {
    &:after {
        content: "";
        background-color: rgba(163, 162, 162, 0.1);
        display: block;
        height: 1px;
        width: 100%;
        margin: 15px auto 15px;
    }
}

i.fab, i.fas, i.fal, i.far {
    color: $main-color;
    font-size: 16px;
    margin: 0px 2px;
    cursor: pointer;
    &:hover {
        opacity: .5;
    }
}
// Heading 2
.hk-h2 {
    color: #fff;
    letter-spacing: 0.1rem;
    position: relative;
    padding: 30px 0px 20px;
    margin-bottom: 30px;
    text-align: center;
    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 100%;
        width: 10%;
        left: 45%;
        margin: auto;
        background-color: $main-color;
        height: 4px;
    }
}

// Check Box inputs
.hk-checkbox {
    display: block;
    position: relative;
    padding-left: 25px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &:hover .checkmark {
        background-color: #ccc;
    }
    input[type='checkbox'] {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        &:checked ~ .checkmark {
            background-color: $main-color;
        }
        &:checked ~ .checkmark:after {
            display: block;
        }
    }
    .checkmark {
        position: absolute;
        top: 2px;
        left: 0;
        height: 15px;
        width: 15px;
        background-color: #eee;

        &::after {
            content: '';
            position: absolute;
            display: none;
            left: 5px;
            top: 2px;
            width: 5px;
            height: 10px;
            border: solid white;
            border-width: 0 3px 3px 0;
            -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }
}

// Inputs
.hk-input {
    background-color: $input-bg;
    border: 0px;
    outline: 0px;
    padding: 10px 15px;
    color: $sm-text-2;
    display: block;
    margin-bottom: 10px;
    width: 100%;
    resize: none;
}
// Icons Sizes
.icon {
    width: 1rem;
    cursor: pointer;
    &:hover {
        opacity: 0.5;
    }
}

.hk-btn,
.hk-btn-g {
    padding: 4px 22px;
    border-radius: 20px;
    color: #fff;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
    transition: all 0.2s ease;
    display: inline-block;
    text-decoration: none;
    &:hover {
        opacity: 0.8;
        text-decoration: none;
        color: #fff;
    }
}
// Buttons With Gradient
.hk-btn-g {
    @include main-gradient(to left);
}
// Buttons
.hk-btn {
    border: 1px solid #fff;
    background-image: transparent;
}

// Bread Crumb
.hk-breadcrumb {
    height: 200px;
    width: 100%;
    background-color: $main-color;
    color: #fff;
    text-align: center;
    padding-top: 40px;
    text-transform: uppercase;

    .bc-header {
        font-weight: bold;
    }
    .bc-list {
        a {
            text-decoration: none;
            &:not(.active) {
                cursor: pointer;
                color: $sm-text-2;
                &::after {
                    content: ' >';
                    display: inline;
                    color: $sm-text-2;
                    font-weight: bold;
                }
                &:hover {
                    color: #fff;
                }
            }
        }
    }
}
// Pagination
.pagination {
    margin: 5px 0px 5px 5px;
    > div {
        cursor: pointer;
        padding: 0px 5px 5px 0px;
        p {
            background-color: $input-bg;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            &:hover,
            &.active {
                background-color: $main-color;
                color: $nav-bg;
                font-weight: bold;
            }
        }
    }
}

.body-card {
    padding: 0px 0px;
    color: #fff;
    width: 85%;
    max-width: 1200px;
    background-color: $nav-bg;
    box-shadow: 0px 8px 24px 0px rgba(0, 0, 0, 0.35);
    margin: -65px auto;
    position: relative;
    z-index: 100;
}
  
@media(max-width: 992px) {
    .body-card {
        margin: -25px auto -65px;
    }
}
#main-footer {
    margin-top: 0px;
    background: url("../img/background/header-bg-1-1.png");
    background-position: 50% 0%;
    background-size: cover;
    border-top: 2px solid $main-color;
    .overlay {
        padding: 10px 0px;
        background-color: rgba(24, 23, 23, 0.8);
        .footer-logo-cont {
            margin: 10px 0px 20px;
            text-align: center;
            img {
                height: 100px;
            }
        }
        h3 {
            color: #fff;
            text-transform: uppercase;
            font-size: 1rem;
            margin-bottom: 25px;
        }
        ul {
            margin: 0px 0px 20px 0px;
            padding: 0px;
            list-style-type: none;
            li {
                margin-bottom: 5px;
                .icon {
                    margin-left: 0px;
                    margin-right: 8px;
                    width: 14px;
                }
                a {
                    color: #fff;
                }
            }
        }
        .bot-footer {
            border-top: 2px solid #555;
            text-align: center;
            color: #eee;
            padding-top: 20px;
            .icons {
                padding-bottom: 10px;
                .icon {
                    width: 50px;
                }
            }
            p {
                margin: 0px;
            }

        }
    }
}
#home-slider {
    .carousel-indicators {
        margin-bottom: 100px;
        li {
            width: 8px;
            height: 8px;
            background-color: transparent;
            border: 3px solid #fff;
            border-radius: 50%;
            &.active {
                background-clip: unset;
                background-color: #fff;
            }
        }
    }
    @media (max-width: 992px) {
        .carousel-indicators {
            margin-bottom: 50px;
        }
    }
    .carousel-control-prev,
    .carousel-control-next {
        top: 50%;
        transform: translate(0%, -50%);
        width: 50px;
        height: 50px;
        background-color: rgba(255, 255, 255, 0.3);
    }
    .carousel-control-next {
        right: 20px;
    }
    .carousel-control-prev {
        left: 20px;
    }
}

#top-nav {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    background: url('../img/background/header-bg-1-1.png');
    background-size: cover;
    background-position: center;
    color: #fff;
    z-index: 500;
    box-shadow: 0px 8px 19px 0px rgba(0, 0, 0, 0.4);
      

    #nav-header {
        height: 100px;
        background-color: rgba(24, 23, 23, 0.8);
        overflow: hidden;
        transition: all 0.4s ease;
        .search-pos {
            flex-grow: 2;
            padding: 0px 10px;
            .nav-search-cont {
                position: relative;
                width: 100%;
                max-width: 350px;
                margin: auto;
                .search {
                    padding: 5px 50px 5px 15px;
                    border: 0px;
                    outline: 0px;
                    width: 100%;
                    background-color: rgba(255, 255, 255, 0.3);
                    color: #d3d3d3;
                    &::placeholder {
                        color: #d3d3d3;
                    }
                }

                .search-i {
                    position: absolute;
                    top: 0px;
                    right: 0px;
                    background-color: transparent;
                    border-left: 1px solid $main-color;
                    width: 32px;
                    padding: 10px;
                }
            }
        }
    }

    #nav-links {
        background: linear-gradient(90deg, $nav-bg 57%, $bg-color 43%);
        @media screen and (max-width: 1200px) {
            background: linear-gradient(90deg, $nav-bg 65%, $bg-color 35%);
        }
        @media screen and (max-width: 991px) {
            background: linear-gradient(90deg, $nav-bg 100%);
        }
        .container {
            padding: 0px;
            display: flex;
            justify-content: space-between;
            ul {
                list-style-type: none;
                float: left;
                margin: 0px;
                padding: 0px;
                & li {
                    float: left;
                    .icon {
                        margin-left: 15px;
                    }
                }
            }
            .navbar {
                padding: 0px;
                @media (max-width: 991px) {
                    width: 100%;
                }
                .navbar-toggler {
                    margin: 10px 15px 10px auto;
                }
                .navbar-nav {
                    width: 100%;
                    .nav-item {
                        @media (min-width: 991px) {
                            margin-right: 20px;
                        }
                        &.active .nav-link,
                        &:hover .nav-link {
                            background-color: $main-color;
                            box-shadow: 0px 0px 2px 0px rgba(0,0,0,.5) inset;
                        }
                        .nav-link {
                            display: inline-block;
                            width: 100%;
                            padding: 15px 10px;
                            color: #fff;
                            text-decoration: none;
                            text-transform: uppercase;
                        }
                    }
                    & > .nav-item:last-of-type a.hk-link {
                        margin-right: 0px;
                    }
                }
            }


            #lang {
                display: flex;
                align-items: center;
                .lang-text {
                    display: inline;
                    padding: 15px 5px 15px 10px;
                    font-family: Roboto-Medium;
                    font-size: 0.8rem;
                    color: $sm-text-1;
                }
                .dropdown {
                    padding: 0px 10px;
                    .dropdown-toggle {
                        background-color: transparent;
                        border: none;
                        border-radius: 0px;
                        padding: 13px 8px;
                        outline: none;
                        color: #fff;
                        &:focus {
                            box-shadow: none;
                        }
                    }

                    .dropdown-menu {
                        min-width: 100%;
                        text-align: center;
                        margin-top: 0px;
                        background-color: $nav-bg;
                        border-radius: 0px;
                        padding: 0px;
                        a {
                            color: #fff;
                            padding: 5px 0px;
                            &:hover {
                                background-color: $main-color;
                            }
                        }
                    }
                }
            }
            .nav-icons {
                display: flex;
                align-items: center;
                i {
                    font-size: 24px;
                    margin: 0px 10px;
                }
            }
        }
    }
}
.nav-pos {
    display: block;
    width: 100%;
    height: 151px;
}
@media (max-width: 992px) {
    .nav-pos {
        height: 160px;
    }
}

.newsletter {
    padding: 100px 0px 50px;
    background: url("../img/background/nl-bg.png");
    background-size: cover;
    background-position: center;
    text-align: center;
    color: #fff;
    .container {
        max-width: 740px;
        p {
            color: $sm-text-2;
        }
        .hk-input {
            border-radius: 10px;
            max-width: 350px;
            display: inline;
        }
        .hk-btn-nl {
            background-color: $input-bg;
            border-radius: 10px;
            cursor: pointer;
            display: inline-block;
            padding: 10px;
            &:hover {
                opacity: .8;
            }
        }
    }
}


.ourstory-p {
    .img-header {
        width: 100%;
    }
    .ourstory {
        padding: 15px 30px;
        h2 {
            text-align: center;
            padding: 50px 0px 0px;
            font-weight: 600;
        }
        .sub-title {
            max-width: 450px;
            margin: 0px auto 20px;
            text-align: center;
        }
        p {
            color: $sm-text-2;
        }
        .capabilities {
            ul {
                margin: 0px;
                padding: 0px;
                text-align: center;
                list-style-type: none;
                margin-top: 20px;
            }
            li {
                padding: 5px 0px;
            }
        }
        .activites {
            margin-top: 40px;
            li {
                margin-bottom: 10px;
            }
        }
        .ref-logo {
            padding: 20px 0px;
            display: flex;
            justify-content: space-between;
            overflow: hidden;
            img {
                max-width: 75px;
            }
        }
    }
}
